*
{
  margin: 0;
  padding: 0;
}
body
{
  background: linear-gradient(90deg, rgba(79,74,68,1) 5%, rgba(45,77,83,1) 90%, rgba(26,88,76,1) 100%, rgba(25,45,40,1) 100%);
}
.App-input
{
  text-align: center;
  height: 300px;
  color: brown;
  font-size: 18px;
  padding: 10px;
  letter-spacing: 2PX;
  margin-top: 15PX;
  font-family: cursive;
}
.App-input input
{
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid rgb(39, 117, 211);
  outline: none;
  padding: 10px 5px;
  margin-top: 15px;
  width: 200px;
}
.App-input button{
  background-color: rgb(159, 201, 215);
  color: rgb(0, 0, 0);
  padding: 10px 20px;
  border-radius: 30px;
  margin-top: 15px;
  font-weight: 700;
}
nav{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0px;
  background-color: rgba(151, 126, 74, 0.592);
  margin-top: 30px;
}
nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 40%;
  color: rgba(20, 9, 23, 0.777);
  font-weight: 700;
}
nav ul li
{
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  font-family: cursive;
  font-size: 15px;
}
nav ul li:hover{
  transform: scale(1.4);
  color: white;
  font-size: 15px;
}
.navActive
{
  color: rgb(230, 34, 34);
}
.App-container
{
  background: rgb(79,74,68);
  background: linear-gradient(90deg, rgba(79,74,68,1) 5%, rgba(45,77,83,1) 90%, rgba(26,88,76,1) 100%, rgba(25,45,40,1) 100%);  color: black;
  padding: 10px;
  font-size: 24px;
  text-align: center;
  margin-top: 18px;
  font-family: cursive;
}